<template>
  <div class="control">
    <Row>
      <Col :xs="{span: 20, offset:2}" :sm="{span: 18, offset:4}" :md="{span:12, offset:6}" :lg="{span: 8, offset:8}" >
        <div class="control-container">
          <div class="controlTitle">服务器接口信息</div>
          <div class="form-container">
            <Form ref="serverInfo" :model="serverInfo">
              <FormItem>
                <Input v-model="serverInfo.serverUrl">
                  <Icon type="ios-link" slot="prepend"/>
                </Input>
              </FormItem>
              <FormItem>
                <Input v-model="serverInfo.serverPort">
                  <Icon type="ios-cloud" slot="prepend"/>
                </Input>
              </FormItem>
              <FormItem>
                <Input v-model="serverInfo.serverGroup">
                  <Icon type="ios-people" slot="prepend"/>
                </Input>
              </FormItem>
              <FormItem style="float: left">
                接口状态：<i-switch v-model="serverInfo.serverPingSwitch" true-color="#13ce66" false-color="#ff4949"></i-switch>
              </FormItem>
            </Form>
            <div class="profileButton">
              <Button type="primary" style="width: 100%" @click="handleEditInfo">修改信息</Button>
            </div>
          </div>
        </div>
      </Col>
    </Row>
  </div>
</template>

<script>
import api from '@/utils/api'
export default {
  name: 'Minecraft',
  data () {
    return {
      serverInfo: {
        serverUrl: '',
        serverPort: '',
        serverGroup: '',
        serverPingSwitch: false
      }
    }
  },
  mounted () {
    api.getServerInfo().then(res => {
      this.serverInfo = res.data.data
    })
  },
  methods: {
    handleEditInfo () {
      console.log(this.serverInfo)
      api.editServerInfo(this.serverInfo).then(res => {
        this.$Message.success('修改成功')
      })
    }
  }
}
</script>

<style scoped>
  .controlTitle{
    font-size: 20px;
    padding: 15px;
  }

  .form-container{
    padding: 10px;
  }

  .control-container{
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 5px;
    margin-top: 50px;
    vertical-align: middle;
  }
</style>



